<template>
  <div>
    <sy-search :af="true" searchType="2" v-on:callBackSearch="searchR"></sy-search>
    <p class="history-record">{{$t('home.search.historyrecord')}}</p>
    <ul>
      <li v-for="(item,index) in historys" :key="index">
        <span>{{item}}</span>
        <img
          @click="remove(index)"
          class="search-icon search-right-icon"
          src="static/images/p_search/search-del.png"
          alt
        >
      </li>
    </ul>
    <img v-if="historys.length>0"
      @click="historyClear()"
      class="history-clear"
      src="static/images/p_search/history-clear.png"
      alt
    >
  </div>
</template>

<script>
import search from "../../components/search.vue";
export default {
  data() {
    return {
      historys: ["aaa","丝绒","儿歌"]
    };
  },
  components: {
    sySearch: search
  },
  created() {
    //接收参数
    let historys = JSON.parse(localStorage.getItem("historys"));
    //localStorage.getItem()方法是获得创建的数组和对象所转化的字符串
    this.historys=historys?historys:[];
  },
  methods: {
    remove(index) {
      this.historys.splice(index, 1);
      localStorage.setItem('historys',JSON.stringify(this.historys))
      //用键值对的方法来实现单个删除，刷新时不会再出现
    },
    historyClear() {
      // this.historys.splice(this.currentIndex, this.historys.length);
      this.historys=[];
      localStorage.removeItem('historys');
      //localStorage.removeItem()方法是清除由localStorage存储的东西自动转换的字符串
    },
    searchR(e) {
      this.historys.unshift(e);
      this.historys = Array.from(new Set(this.historys));
      localStorage.setItem('historys',JSON.stringify(this.historys));
      // console.log(this.historys);
      //localStorage.setItem()方法是创建一个数组或对象，且转换为字符串
      // JSON.stringify() 方法是将一个JavaScript值(对象或者数组)转换为一个 JSON字符串
    }
  },
  watch:{
    historys:{
      handler:function(newVal, oldVal){
        if(this.historys.length>5){
          this.historys.pop();
        }
        return JSON.parse(localStorage.getItem('historys'));
        //JSON.parse() 方法用来解析JSON字符串，构造由字符串描述的JavaScript值或对象。
      }
    },
    deep:true
  }
};
</script>

<style lang="scss" scoped>
.history-record {
  font-size: 0.79rem;
  letter-spacing: 0.003rem;
  margin: 0 0 1.156rem 0.75rem;
  opacity: 0.6;
}
ul li {
  display: flex;
  justify-content: space-between;
  height: 1.8rem;
  line-height: 1.6rem;
  border: 1px solid lightgray;
  margin: 0 0.75rem;
}
span {
  font-size: 0.75rem;
}
.search-icon {
  width: 0.75rem;
  height: 0.719rem;
  margin: auto 0.75rem;
}
.history-clear {
  display: block;
  width: 10.5rem;
  margin: 2rem auto;
}
</style>



